import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

@Component({
  selector: 'app-navbar',
  templateUrl: './navbar.component.html',
  styleUrls: ['./navbar.component.scss']
})
export class NavbarComponent implements OnInit {
  constructor(private router: Router) {}

  ngOnInit(): void {}

  gotoHome() {
    return this.router.navigate(['/']);
  }

  gotoTechnology() {
    return this.router.navigate(['/tech']);
  }

  gotoSports() {
    return this.router.navigate(['/sports']);
  }
  gotoScience() {
    return this.router.navigate(['/science']);
  }
  gotoHealth() {
    return this.router.navigate(['/health']);
  }

  gotoGeneral() {
    return this.router.navigate(['/general']);
  }
  gotoEntertainment() {
    return this.router.navigate(['/entertainment']);
  }
  gotoBusiness() {
    return this.router.navigate(['/business']);
  }
}
